import { Avatar } from '@material-ui/core'
import React from 'react'
import './SideBar.css'

function SideBar({imageUrl,userName,userId,pUserName,pUserUrl}) {
    return (
        <div className="sideBar__main__container">
            <div className="profile__avater__container">
                <div className="profile__avater">
                    <div>
                        <Avatar src={imageUrl} />
                    </div>
                    <div className="userItext">
                        <div>{userName}</div>
                        <div>{userId}</div>
                    </div>
                </div>
                <div><p><span className="span">Switch</span></p></div>
            </div>
            <div className="suggested__div">
                <div>suggested for you</div>
                <div>see all</div>
            </div>

{/* this the first line of the code and reaer */}

            <div className="follow__tooltip">
                <div className="avater__name">
                    <div><Avatar src={pUserUrl} /></div>
                    <div className="userName__tooltip"><p>{pUserName}</p></div>
                </div>
                <div><span className="span">Follow</span></div>
            </div>  
{/* this is the lat video of the tutorial of the  */}
            <div>
                <div>
                    <p>this is the jpara graph for the people and lvoe you guise ummah</p>
                </div>
                <div>
                    <p>this is the copyright and 2009</p>
                </div>
            </div>
        </div>
    )
}

export default SideBar
