import React,{useState} from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import AddBoxOutlinedIcon from '@material-ui/icons/AddBoxOutlined';
import Axios from './Axios';
import "./Header.css"

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
//   border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  

  const [user, setuser] = useState([])
  const [file, setfile] = useState([])
  const [userprofileIds, setuserprofileIds] = useState([])
  const [fileName, setfileName] = useState([])
  const [status, setstatus] = useState("")





  const submitPost = (e) => {
    console.log(file)
    if(file&&status){
      const data = new FormData() 
      data.append("status",status)
      data.append('file', file)
      data.append("UserName",userprofileIds.username)
      console.warn(file);
      console.log(user)
      Axios.post("/upload/single", data,{
        // UserName:"mahfuz Rana",
      })
      .then(res => { // then print response status
          console.warn(res);
      })
    }
    else{
      alert("please do someting before you submit");
    }

    setOpen(false)
    console.log(fileName)
    // setstatus(null)
  }

  Axios.get("/authenticatduser",{
    headers: {
      authorization:'Bearer ' + localStorage.getItem('token')
    }
  }).then(
  res => {
    // res.send("NICE")
    // console.log("NICE")
    setuserprofileIds(res.data)
    setuser(res.data)
  },
  err => {
    console.log(err)
  }
  )
  const uplaodStatus = (e) =>{
    // setstatus()
      setstatus(e.target.value)
  }


  return (
    <div>
        <div className="centeringabutton">
        <AddBoxOutlinedIcon  onClick={handleOpen}/>
        </div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <input type="text" placeholder="Enter your status" onChange={uplaodStatus} />
      <input type="file" onChange={(e)=>{setfile(e.target.files[0])
        console.log(e.target.files[0])
        console.log(file)
        }} />
      <input type="button" value="post" onClick={submitPost} />
        </Box>
      </Modal>
    </div>
  );
}