import { Avatar, IconButton } from '@material-ui/core'
import React,{useState,useEffect} from 'react'
import './Post.css'
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import ShareIcon from '@material-ui/icons/Share';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import Axios from "./Axios.js"
import Comment from './Comment';
// import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteIcon from '@mui/icons-material/Favorite';



function Post({imageUrl,username,postStatus,likes,comment,postId}) {
  
const [clicked, setclicked] = useState(null)
const [count, setcount] = useState(0)
const [user, setuser] = useState(null)
const [comments, setcomments] = useState(null)
const [clickedComment, setclickedComment] = useState(null)
const [storedId, setstoredId] = useState(null)

Axios.get("/authenticatduser",{
  headers: {
    authorization:'Bearer ' + localStorage.getItem('token')
  }
}).then(
res => {
  // res.send("NICE")
  // console.log("NICE")
  // setuserprofileIds(res.data)
  setuser(res.data)
},
err => {
  console.log(err)
}
)


const liked = ()=>{
  if(user){
    setclicked(null)
    setcount(count-1)
    console.log(count)
    const data = new FormData() 
data.append('likes', count)
console.warn(count);
// console.log(user)
Axios.post("/upload/single", data,{
  // UserName:"mahfuz Rana",
})
.then(res => { // then print response status
    console.warn(res);
})


  }
  else{
    alert("Please login")
  }
}

const commentsPost = (e) => {
  console.log(postId)
  if(!storedId){
    setstoredId(postId)
  }
  else{
    console.log("please set the Id ")
  }
  console.log(storedId)
    if(user&&storedId){
  //     const data = new FormData() 
  // data.append('comments', comments)
  // data.append('id', storedId)
  if(storedId){
    Axios.post("/upload/single/update", {
      id:storedId,
      comments:comments
    })
    .then(res => {
        // this.setState({ users: response.data });
        console.log(res);
    });
  }else{
    console.log("something wrong")
  }

}
    else{
      // alert("Please login")
      console.log("not allowed")
    }

    // alert(postId)
    // e.target.value("")
    // setstoredId(null)
    setclickedComment(null)
    // setstoredId(null)


}







return (
        <div className="post__header__container">
          {/* <h1>love</h1>   */}
          <div className="post__header">
              <div>
                  <IconButton>
                    <Avatar />
                  </IconButton>
                  <p className="userName">{username}</p>
              </div>
              <div>
                <MoreHorizIcon />
              </div>
          </div>
          <div className="post__image">
            <img src={`images/${imageUrl}`} />
          </div>
        <div className="post__react">
            <div >
      <IconButton >
      {clicked?  
                  (<FavoriteIcon sx={{ fontSize: 30 }} color="primary" onClick={liked}/>):(
                    <FavoriteBorderIcon onClick={()=>{
                    if(user){
                      setclicked("clicked") 
                      setcount(count+1)
                      console.log(count)
                    }
                    else{
                      alert("please login")
                    }
                  }}/>
                  )}
      </IconButton>
                
                {/* <IconButton><ShareIcon /></IconButton> */}

            </div>
            <div>
                {/* <IconButton><BookmarkBorderIcon /></IconButton> */}
                <IconButton><ChatBubbleOutlineIcon /></IconButton>
            </div>
        </div>
        <div className="likesandComments" >
            <div>caption: {postStatus}</div>
          </div>
          <div className="likesandComments">
             <div>{likes}<span>likes{}</span></div>
             <div>{likes}<span>Comments </span></div>
          </div>
          <Comment comment={comment}/>
          <div className="Post__footer">
               <div>
                    <IconButton>
                        <SentimentVerySatisfiedIcon />
                    </IconButton>
               </div>
               <div className="input__container">
                   <input placeholder="Write something here..." onChange={(e) => {
                    setcomments(e.target.value)
                    setclickedComment("clicked")
                    }} />
               </div>
               <div className="post__comment">
                <button type="submit" onClick={commentsPost}>Post</button>                   
               </div>
          </div>
        </div>
    )
}

export default Post
